//Titles
.title-xl
    @apply font-magnatText font-medium text-[2rem] md:text-[4.6875rem] leading-[122%] uppercase

.title-md
    @apply font-magnatText font-normal text-[1.5rem] md:text-[3rem] lg:text-[3rem] leading-[125%] tracking-[0.03125rem] uppercase md:normal-case
    
.title-sm
    @apply font-magnatText font-normal text-[1.125rem] md:text-[1.5rem] leading-none tracking-[0.0003125rem]
    
.title-30
  @apply font-magnatText font-normal text-[24px] lg:text-[1.875rem] leading-[150%] tracking-[-0.03563rem] uppercase

//Body text
.block-text
    @apply font-funnelsans font-normal text-[0.875rem] md:text-[1.0625rem] leading-[1.125rem] md:leading-[1.625rem] tracking-[0.0313rem]

.block-17
  @apply font-funnelsans  font-normal text-[14px] md:text-[1.0625rem] leading-[1.625rem] tracking-[0.0313rem]

.block-30
  @apply font-funnelsans font-normal text-[16px] md:text-[1.875rem] leading-[150%] tracking-[-0.03563rem]

.block-text-intro
    @apply font-funnelsans font-normal text-[1.5rem] md:text-[2.1875rem] leading-[2.1875rem] md:leading-[2.6875rem] tracking-[0.0313rem]

.text-inline-link
    @apply transition-all duration-500 ease-in-out hover:text-coral